a.btn,
.btn {
  background-color: #4772D9;
  color: white;
  margin: 20px auto 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  text-decoration: none;
  font-size: 18px;
  border: 2px solid #4772D9;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: color, background-color;
  transition-property: color, background-color;
  height: 7vh;
  min-height: 45px;
  width: 200px;
  min-width: 20vw;
  font-size: calc(0.5vw + 16px);
}
a.btn:hover,
.btn:hover {
  -webkit-animation: shadow-drop-2-center 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: shadow-drop-2-center 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  background-color: white;
  color: #4772D9;
  cursor: pointer;
}

.btn--invert {
  background-color: white;
  color: #4772D9;
}
.btn--invert:hover {
  background-color: #4772D9;
  color: white;
}

@-webkit-keyframes shadow-drop-2-center {
  0% {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
  }
  100% {
    -webkit-transform: translateZ(50px);
    transform: translateZ(50px);
    -webkit-box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.25);
    box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.25);
  }
}
@keyframes shadow-drop-2-center {
  0% {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
  }
  100% {
    -webkit-transform: translateZ(50px);
    transform: translateZ(50px);
    -webkit-box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.25);
    box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.25);
  }
}
.clickable,
a:not(.btn) {
  background: -webkit-gradient(linear, left top, left bottom, from(#4772D9), to(#4772D9));
  background: linear-gradient(to bottom, #4772D9 0%, #4772D9 100%);
  background-position: 0 100%;
  background-repeat: repeat-x;
  background-size: 4px 4px;
  color: #000;
  text-decoration: none;
  -webkit-transition: background-size 0.3s, color 0.3s;
  transition: background-size 0.3s, color 0.3s;
}

.clickable:hover,
a:hover {
  cursor: pointer;
  background-size: 4px 90%;
  color: white;
}

